<template>
  <!--
    	作者：luoyiming
    	时间：2019-10-24
    	描述：后台系统首页
    -->
    <div class="home-container">
      <h1 class="home-title">
            后台运营管理系统
      </h1>
      <p class="home-des">
        尊敬的 {{username}}  用户，欢迎使用后台管理员系统
      </p>

    </div>
</template>

<script>
  import { delCookie, getCookie } from '@/utils/base.js';
  import UserApi from '@/api/user.js';
  export default {
    data() {
      return {
        username:'',
          version:'',
      };
    },
    created() {
      /*获取登录用户名*/
      this.username= getCookie('userinfo');
      /*获取数据*/
      this.getTableList();
    },
      methods: {
        /*获取数据*/
          getTableList() {
              let self = this;
              let Params = {};
              UserApi.getVersion(Params, true).then(data => {
                      self.loading = false;
                      self.version=data.data.version;
                  })
                  .catch(error => {
                      self.loading = false;
                  });
          },
      }
  }
</script>

<style lang="scss">
  .home-container {
    height: calc(100vh - 120px);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    align-content:center;
    flex-direction: column;
    // background: url(/static/imgs/home_bg.jpg) center center no-repeat;
  }
.home-container .home-title{
  padding: 20px;
  text-align: center;
 white-space: nowrap;
  text-align: center;
  font-size: 40px; color:#409EFF;}
.home-container .home-des{color: #888888;}

  .home-index{
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      min-width: 1000px;
      overflow-x: auto;
  }
</style>



